import {
    register
} from '@/api/user'
export default {
    name: 'Login',
    components: {},
    data() {
        return {
            user: {
                "username": "",
                "email": "",
                "password": ""
            },
            errors: {}
        }
    },
    computed: {
        isLogin() {
            return this.$route.name === 'login'
        }
    },
    methods: {
        async onSubmit() {
            try {
                const {
                    data
                } = await register({
                    user: this.user
                })
            } catch (error) {
                this.errors = err.response.data.errors
            }
        },
    }
}
